import '../styles/header.css';
import Query from './Query/index.js';
import HEADER_QUERY from '../queries/header.js';
import { connect } from "react-redux";
import store from '../store/index';
import { slide as Menu } from 'react-burger-menu';
import { NavHashLink } from 'react-router-hash-link';

const mapStateToProps = state => {
    return { lang: state.lang };
};

function changeLang() {
    var state = store.getState()
    var lang = state.lang === 'en' ? 'hun' : 'en';

    store.dispatch({
        type: 'SET_LANG',
        payload: lang
    });
}

function closeMenu() {
    function eventFire(el, etype){
        if (el.fireEvent) {
          el.fireEvent('on' + etype);
        } else {
          var evObj = document.createEvent('Events');
          evObj.initEvent(etype, true, false);
          el.dispatchEvent(evObj);
        }
      }
      eventFire(document.getElementById('react-burger-cross-btn'), 'click');
/*     let element = document.getElementById('react-burger-cross-btn');
    element.dispatchEvent(new KeyboardEvent('keydown',{'key':'Escape'})); */
}


const connectedHeader = ({ lang }) => {
    return (
        <div>
            <nav class="navbar fixed-top navbar-expand-xl navbar-light bg-lightest" id="header">
                <NavHashLink smooth
                    to="/#home"
                    className="navbar-brand"
                ><img src="/logo.svg" alt="Yolk.digital"/></NavHashLink>
                
                <Query query={HEADER_QUERY} id={null} >
                    {({ data: { header } }) => {
                        return (
                            <>
                                <div class="d-xl-none">
                                    <img src="/menu.svg" alt="Menu"/>
                                        <Menu right width={ 280 } disableAutoFocus >
                                                {header.links.map((link, i) => {
                                                    return (
                                                        <div class="nav-item mr-4">
                                                            <NavHashLink smooth
                                                                onClick={closeMenu}
                                                                to={link.url}
                                                                className="nav-link"
                                                                activeClassName="nav-selected"
                                                            >{link[lang]}</NavHashLink>
                                                        </div>
                                                    )
                                                })}
                                                <div class="nav-item">
                                                    <a 
                                                        class="nav-link"
                                                        onClick={changeLang}
                                                        >
                                                            {lang === 'hun' &&
                                                                <span>EN/<span class="text-orange">HU</span></span>
                                                            }
                                                            {lang === 'en' &&
                                                                <span><span class="text-orange">EN</span>/HU</span>
                                                            }
                                                    </a>
                                                </div>
                                        </Menu>
                                </div>
                                <div class="collapse navbar-collapse" id="navbarSupportedContent">
                                    <ul class="navbar-nav ml-auto">
                                        {header.links.map((link, i) => {
                                            return (
                                                <li class="nav-item mr-4">
                                                    <NavHashLink smooth
                                                        to={link.url}
                                                        className="nav-link"
                                                        activeClassName="nav-selected"
                                                    >{link[lang]}</NavHashLink>
                                                </li>
                                            )
                                        })}
                                        <li class="nav-item">
                                            <a 
                                                class="nav-link"
                                                onClick={changeLang}
                                                >
                                                    {lang === 'hun' &&
                                                        <span>EN/<span class="text-orange">HU</span></span>
                                                    }
                                                    {lang === 'en' &&
                                                        <span><span class="text-orange">EN</span>/HU</span>
                                                    }
                                            </a>
                                        </li>
                                    </ul>
                                </div>
                            </>
                        )
                    }}
                </Query>
            </nav>
        </div>
    )
}

const Header = connect(mapStateToProps)(connectedHeader);

export default Header
